<template>
    <div>
        <div class="block">
            <span class="demonstration"></span>
            <el-date-picker
                    v-model="value3"
                    type="week"
                    format="yyyy  WW "
                    placeholder="">
            </el-date-picker>
        </div>
        <div class="block">
            <span class="demonstration"></span>
            <el-date-picker
                    v-model="value4"
                    type="month"
                    placeholder="">
            </el-date-picker>
        </div>
        <div class="block">
            <span class="demonstration"></span>
            <el-date-picker
                    v-model="value5"
                    align="right"
                    type="year"
                    placeholder="">
            </el-date-picker>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                value3: '',
                value4: '',
                value5: ''
            };
        }
    };
</script>


