<template>
    <el-select v-model="value8" filterable placeholder="">
        <el-option
                v-for="item in options"
                :label="item.label"
                :value="item.value">
        </el-option>
    </el-select>
</template>

<script>
    export default {
        data() {
            return {
                options: [{
                    value: '1',
                    label: ''
                }, {
                    value: '2',
                    label: ''
                }, {
                    value: '3',
                    label: ''
                }, {
                    value: '4',
                    label: ''
                }, {
                    value: '5',
                    label: ''
                }],
                value8: ''
            }
        }
    }
</script>


