<template>
    <div id="app">
        <el-row>
            <el-col :span="24">
                <div class="header-bg">
                    <a href="/">
                        <img src="./assets/logo.png"
                             alt="logo"
                             class="header-logo">
                    </a>
                    <ul class="header-operations">
                        <li></li>
                        <li class="header-download"></li>
                        <li></li>
                    </ul>
                </div>
            </el-col>
            <el-col :span="4">
                <div>
                    <el-menu default-active="2" unique-opened router>
                        <el-submenu :index="menu.code" v-for="menu in meuns">
                            <template slot="title"><i :class="menu.icon"></i>{{menu.name}}</template>
                            <el-menu-item v-for="child in menu.childrens" :index="child.code">{{child.name}}</el-menu-item>
                        </el-submenu>
                    </el-menu>
                </div>
            </el-col>
            <el-col :span="20">
                <div class="c-content-s">
                    <router-view></router-view>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                meuns: [{
                    name: '',
                    code: 'base',
                    icon: 'el-icon-menu',
                    childrens: [
                        {name: 'Layout ', code: 'Layout'},
                        {name: 'Color ', code: 'Color'},
                        {name: 'Typography ', code: 'Typography'},
                        {name: 'Icon ', code: 'Icon'},
                        {name: 'Button ', code: 'Button'}]
                }, {
                    name: '',
                    code: 'form',
                    icon: 'el-icon-edit',
                    childrens: [
                        {name: 'Radio ', code: 'Radio'},
                        {name: 'Checkbox ', code: 'Checkbox'},
                        {name: 'Input ', code: 'Input'},
                        {name: 'InputNumber ', code: 'InputNumber'},
                        {name: 'Select ', code: 'Select'},
                        {name: 'Cascader ', code: 'Cascader'},
                        {name: 'Switch ', code: 'Switch'},
                        {name: 'Slider ', code: 'Slider'},
                        {name: 'TimePicker ', code: 'TimePicker'},
                        {name: 'DatePicker ', code: 'DatePicker'},
                        {name: 'DateTimePicker ', code: 'DateTimePicker'},
                        {name: 'Upload ', code: 'Upload'},
                        {name: 'Rate ', code: 'Rate'},
                        {name: 'ColorPicker ', code: 'ColorPicker'},
                        {name: 'Form ', code: 'Form'}]
                }, {
                    name: '',
                    code: 'dataGrid',
                    icon: 'el-icon-document',
                    childrens: [
                        {name: 'Table ', code: 'Table'},
                        {name: 'Tag ', code: 'Tag'},
                        {name: 'Progress ', code: 'Progress'},
                        {name: 'Tree ', code: 'Tree'},
                        {name: 'Pagination ', code: 'Pagination'},
                        {name: 'Badge ', code: 'Badge'}]
                }, {
                    name: '',
                    code: 'Notice',
                    icon: 'el-icon-information',
                    childrens: [
                        {name: 'Alert ', code: 'Alert'},
                        {name: 'Loading ', code: 'Loading'},
                        {name: 'Message ', code: 'Message'},
                        {name: 'MessageBox ', code: 'MessageBox'},
                        {name: 'Notification ', code: 'Notification'}]
                }]
            }
        },
        methods: {}
    }
</script>

<style>
    body {
        margin: 0px;
        padding: 0px;
        font-family: Helvetica, sans-serif;
    }

    .header-bg {
        background-color: #00a6ea;
    }

    .header-logo {
        height: 45px;
        padding: 5px;
        margin-left: 20px;
    }

    .header-operations {
        float: right;
    }

    .header-operations li {
        float: left;
    }

    .c-content-s {
        padding: 10px;
    }
</style>




